<template>
  <div id="app">
    <transition :name="transitionName">
      <router-view/>
    </transition>
    <home-footer v-if="$route.meta.show"></home-footer>
  </div>
</template>

<script>
  import homeFooter from './components/footer/footer.vue'

  export default {
    name: 'App',
    data() {
      return {
        transitionName: 'slide-right'
      }
    },
    components: {
      homeFooter
    },
    beforeRouteEnter(to, from, next) {
      console.log(11111);
      next(vm => vm.routerMove())
    },
    updated() {
      try {
        let wechatTitle = document.getElementById('header').getElementsByClassName('title-text')[0].innerText;
        document.title = wechatTitle
      } catch (err) {
        document.title = '永昌移动商城'
      }
    },
    methods: {
      routerMove() {
        // 如果isBack为true时，证明是用户点击了回退，执行slide-right动画
        let isBack = this.$router.isBack;
        console.log(isBack);
        if (isBack) {
          this.transitionName = 'slide-right'
        } else {
          this.transitionName = 'slide-left'
        }
        // 做完回退动画后，要设置成前进动画，否则下次打开页面动画将还是回退
        this.$router.isBack = false
      }
    }
  }
</script>

<style lang="scss">
  @import './style/common';

  html, body, #app {
    width: 100vw;
    height: 100%;
    overflow-x: hidden;
    display: block;
    position: relative;
  }

  .slide-right-enter-active, .slide-right-leave-active, .slide-left-enter-active, .slide-left-leave-active {
    transform: translateX(0);
    transition: all 0.2s;
    opacity: 1;
    width: 100vw!important;
  }

  .slide-right-enter, .slide-left-leave {
    transform: translateX(100vw);
    opacity: 0;
    width: 100vw!important;
  }

  .slide-right-leave-to, .slide-left-enter {
    transform: translateX(-100vw);
    opacity: 0;
    width: 100vw!important;
  }


</style>



