// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/'
import axios from 'axios'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import './style/mymint.scss';
import {Indicator} from 'mint-ui'
import Qs from 'qs'
import FastClick from 'fastclick'
// import VueLazyload from 'vue-lazyload'  //引入这个懒加载插件


if ('addEventListener' in document) {
  document.addEventListener('DOMContentLoaded', function () {
    FastClick.attach(document.body);
  }, false);
}


Vue.config.productionTip = false
axios.defaults.withCredentials = true;


Vue.use(MintUI);
// Vue.use(VueLazyload)
// 或者添加VueLazyload 选项
// Vue.use(VueLazyload, {
//   preLoad: 1,
//   error: '../static/img/m1.jpg',
//   loading: '../static/img/imgloading.gif',
//   attempt: 1
// })

 Vue.prototype.host = 'http://172.16.1.55:8080/yc-shop/api';
 // Vue.prototype.host = 'http://t.2858.com/yc-shop/api';
// Vue.prototype.imgHost = 'http://localhost:8080/static/';
// Vue.prototype.imgHost = 'http://t.2858.com/ycshop';
Vue.prototype.imgHost = 'http://172.16.1.55:8080/yc-shop';

Vue.prototype.hrefName = function (name) {
  this.$store.commit('setActive', name);
  this.$router.push({name: name});
}

Vue.prototype.bindPathName = function (name) {
  if (this.$route.query.goback) {
    this.$router.push({name: name, query: {goback: this.$route.query.goback, goto: this.$route.query.goto}})
  } else {
    this.$router.push({name: name})
  }
}


//综合请求
Vue.prototype.ajax = function (options) {
  let that = this;
  Indicator.open();
  axios({
    url: that.host + options.url,
    method: options.method || 'GET',
    headers: options.headers || null,
    data: options.data ? Qs.stringify(options.data) : null,
  }).then(function (res) {
      console.log(res)
      if (res.status == 200) {
        options.success(res.data)
        if (res.data.status == 101) {
          window.localStorage.removeItem('memberId');
          window.localStorage.removeItem('username');
          that.$router.push({path: '/index'})
        }
      }
      Indicator.close();
    }
  ).catch(function (error) {
    Indicator.close();
    console.log(error);
    // options.error(error)
  });
}


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
})



